import L from 'leaflet';
import { MapContainer, useMap, useMapEvent, TileLayer, Marker, Popup } from 'react-leaflet';
import { useRef, useState, useEffect, useMemo } from 'react';
import LocationIcon from '../icons/LocationIcon'
import logo from "../pages/image/logo.svg"
import logos from "../pages/image/lhome.png"

import { Icon } from 'react-icons-kit'
import {home} from 'react-icons-kit/fa/home'

function MapSingle({products}){
    const myIcon = L.icon({
        iconUrl: 'https://gamysh.com/uploads/images/lhome.png',
        iconSize: [48,48],
        iconAnchor: [32, 64],
        popupAnchor: null,
        shadowUrl: null,
        shadowSize: null,
        shadowAnchor: null
    });
    console.log(products,"map page");
    console.log(products?.position[0]?.lat,"map page");
    console.log(products?.position[0]?.lng,"map page");


    return (
        // <div >
            <MapContainer 
                center={products ? [products?.position[0]?.lat, products?.position[0]?.lng] : null} 
                zoom={15} scrollWheelZoom={true}
                style={{height:450}}
                >
                    
                    {/* <LocationIcon className="absolute text-gray-800 top-1/2 left-1/2 text-5xl -mt-11 -ml-6" style={{zIndex:100000}}/> */}

                    <TileLayer

                        url="https://gamysh.com/map/tile/{z}/{x}/{y}.png"
                        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    />
                <Marker 
                position={products ? [products?.position[0]?.lat, products?.position[0]?.lng] : null}  
                icon={myIcon
                // <Icon size={25} icon={home} className="text-blue-600" />
            }
                >
                {/* <Popup>
                    your home location
                    </Popup> */}
                </Marker>
            </MapContainer>
        // </div>
    );
}

export default MapSingle;